<template>
  <v-dialog
    v-model="dialogIsOpen"
    @input="v => v || closeDialog()"
  >
    <div class="product-categories-dialog-component">
      <v-card class="dialog-card">
        <v-layout justify-end>
          <v-icon
            x-large
            @click="closeDialog"
            absolute
          >
            close
          </v-icon>
        </v-layout>
        <v-layout justify-center>
          <h2 class="headline">Choose a Surface</h2>
        </v-layout>
        <v-layout row justify-space-around wrap>
          <v-flex xs6 sm4 md3 lg2 v-for="item in productSubcategories" :key="item.slug">
            <v-card flat class="category-card ma-3"  @click="selectProductCategory(item.slug)">
              <v-img :src="item.image"></v-img>
              <div class="mt-3">
                <span>{{ item.name }}</span>
              </div>
            </v-card>
          </v-flex>
        </v-layout>

      </v-card>
    </div>
  </v-dialog>
</template>

<script>
import Backend from '../services/backend';

const backend = new Backend();

export default {
  name: 'ProductCategoriesDialog',
  props: ['dialogIsOpen', 'currentProductCategory'],
  components: {
  },
  computed: {
    productSubcategories() {
      return this.currentProductCategory && this.currentProductCategory.subcategories || [];
    }
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'designCategories' });
  },
  methods: {
    selectProductCategory(productCategory) {
      this.$router.push({
        name: 'productCategory',
        params: {
          ...this.$router.params,
          productCategory: productCategory,
        }
      });
      this.closeDialog();
    },
    closeDialog() {
      this.$emit('closeDialog');
    }
  }
};
</script>

<style lang="scss">
.product-categories-dialog-component {
  .dialog-card {
    padding: 30px 20px;
  }
  .category-card {
    cursor: pointer;
  }
}
</style>
